@charset "utf-8";

// ---------------- Sass ----------------
@use "sass:meta";
@use "sass:map";
@use "sass:list";

// ----------------  ----------------
@use 'setting/setting' as *;

// ----------------  ----------------

// px  vw 
@function get_vw($size, $viewport: $vp-sp) {
  @return calc($size / $viewport * 100vw);
}

// 
@function get_min($size, $viewport: $vp-pc) {
  @return min(#{get_vw($size, $viewport)}, #{$size * 1px});
}

// 
@function get_max($size, $viewport: $vp-pc) {
  @return max(#{get_vw($size, $viewport)}, #{$size * 1px});
}

// px  vh 
@function get_vh($size, $viewport: $vp-sp) {
  @return calc($size / $viewport * 100vh);
}

// ----------------  ----------------

// flexcenter
@mixin center() {
  display: flex;
  align-items: center;
  justify-content: center;
}

// after
@mixin after() {
  &:after {
    display: block;
    content: '';
    @content;
  }
}

// before
@mixin before() {
  &:before {
    display: block;
    content: '';
    @content;
  }
}

//btn
@mixin btn-w () {
  border: $c_white solid;
  border-width: 0;
  border-radius: get_vw(25);
  background-color: $c_white;
  width: get_vw(350);
  height: get_vw(50);
  margin-left: auto;
  margin-right: auto;
  @include center;
  position: relative;
  @include pc-fluid-size(border-width, (4));
  @include pc-fluid-size(border-radius, (25));
  @include pc-fluid-size(width, (260));
  @include pc-fluid-size(height, (50));

  @include min-screen() {
    transition: $transition;

    &:hover {
      background-color: $c_ivory;
    }
  }

  span {
    font-size: get_vw(14);
    font-weight: $w_medium;
    display: flex;
    align-items: center;
    position: relative;
    @include pc-fluid-size(font-size, (14));
  }

  @include after() {
    border-radius: 50%;
    background-image: url('../data/webp/common/icon_arw_btn.webp');
    background-position: center;
    background-repeat: no-repeat;
    background-size: get_vw(15) auto;
    background-color: $c_ivory;
    width: get_vw(40);
    height: get_vw(40);
    position: absolute;
    top: get_vw(5);
    right: get_vw(5);
    @include pc-fluid-bg-size(15, auto);
    @include pc-fluid-size(width, (40));
    @include pc-fluid-size(height, (40));
    @include pc-fluid-size(top, (1));
    @include pc-fluid-size(right, (3));

    @include min-screen() {
      transition: $transition;
    }
  }
}

